    setPlantList(paramPlantList) {
      //              start
      this.dataPlantList = {};
      var idx = 0;
      var dataPlantListTmp = this.dataPlantListOri;

      if(paramPlantList.length > 0) {
        // 24.06.14  json   -  
        // json    , index     ?  db   plnt_id     ?
        for(var i = 0; i < paramPlantList.length; i++) {
          for(var j = 0; j < dataPlantListTmp.length; j++) {
            // console.log('keywordPlantList[i] == dataPlantListTmp[j].plnt_id :: ', keywordPlantList[i] == dataPlantListTmp[j].plnt_id);
            if (paramPlantList[i].plnt_id == dataPlantListTmp[j].plnt_id) {
              this.dataPlantList[idx] = dataPlantListTmp[j];
              idx++;
            }
          }
        }
      } else {
        this.dataPlantList = null;
      }

      //              end
      
      this.setPlantListMode = true;
      this.setListAll = true;
    },
    showPlantContent(plantInfo) {
      this.setPlantListMode = false;
      this.paramPlantInfo = plantInfo;

      this.grwng_min_tmprtr_val = plantInfo.grwng_min_tmprtr_val;
      this.grwng_max_tmprtr_val = plantInfo.grwng_max_tmprtr_val;
    },
    getKeywordPlantList(keywordInfo) {
      //      keywordPlantList  .
      var keywordPlantList = [];
      this.dataPlantList = {};
      
      
      // console.log('240625 PlantIntro.vue : getKeywordPlantList : dataKeywordPlantList ::::: ', this.dataKeywordPlantList);

      for (var i = 0; i < this.dataKeywordPlantList.length; i++) {
        if (this.dataKeywordPlantList[i].cdtyp_cd == keywordInfo.cdtyp_cd
                    && this.dataKeywordPlantList[i].cd == keywordInfo.cd) {
          for(var j = 0; j < this.dataKeywordPlantList[i].plnt_list.length; j++) {
            keywordPlantList.push(this.dataKeywordPlantList[i].plnt_list[j].plnt_id);
          }
        }
      }

      // console.log('240625 PlantIntro.vue : getKeywordPlantList : keywordPlantList ::::: ', keywordPlantList);

      //  plant_id        .
      // var dataPlantListTmp = plantListJson;  // json  
      var dataPlantListTmp = this.dataPlantListOri;
      var idx = 0;

      for(i = 0; i < keywordPlantList.length; i++) {
        for(j = 0; j < dataPlantListTmp.length; j++) {
          // console.log('keywordPlantList[i] == dataPlantListTmp[j].plnt_id :: ', keywordPlantList[i] == dataPlantListTmp[j].plnt_id);
          if (keywordPlantList[i] == dataPlantListTmp[j].plnt_id) {
            this.dataPlantList[idx] = dataPlantListTmp[j];
            idx++;
          }
        }
      }
      this.keywordInfo = keywordInfo;
      this.setPlantListMode = true; 
      this.setListAll = false;
      this.setListGb = 'Keyword';
    },
    getTmprStndPlantList(tmprtr_clss) {
      //        PlantList  .
      this.dataPlantList = {};
      var idx = 0;
      var dataPlantListTmp = this.dataPlantListOri;

      for (var i = 0; i < dataPlantListTmp.length; i++) {
        // console.log('dataPlantListTmp[i].grwng_avg_tmprtr_val = ', dataPlantListTmp[i].grwng_avg_tmprtr_val);
        if (dataPlantListTmp[i].tmprtr_clss == tmprtr_clss) {
          this.dataPlantList[idx] = dataPlantListTmp[i];
            idx++;
        } 
      }
      this.setPlantListMode = true; 
      this.setListAll = false;
      this.setListGb = 'Temperature';
    },
    getLightStndPlantList(lght_clss) {
      //        PlantList  .
      this.dataPlantList = {};
      var idx = 0;
      var dataPlantListTmp = this.dataPlantListOri;

      for (var i = 0; i < dataPlantListTmp.length; i++) {
        // console.log('dataPlantListTmp[i].grwng_avg_tmprtr_val = ', dataPlantListTmp[i].grwng_avg_tmprtr_val);
        if (dataPlantListTmp[i].lght_clss == lght_clss) {
          this.dataPlantList[idx] = dataPlantListTmp[i];
            idx++;
        } 
      }
      this.setPlantListMode = true; 
      this.setListAll = false;
      this.setListGb = 'Light';
    },
    showAllPlantList(sortTyp) {
      if(sortTyp == 'B') {
        // order by age asc
        // array.sort((a, b) => {
          this.dataPlantList = [...this.dataPlantListOri].sort((a, b) => {
            // a.xxx =>     .
            //    if  .      .
            if (a.plnt_nm < b.plnt_nm) return -1;
            if (a.plnt_nm > b.plnt_nm) return 1;

            return 0;
        });
      } else if (sortTyp == 'C') {
          this.dataPlantList = [...this.dataPlantListOri].sort((a, b) => {
            if (a.grwng_avg_tmprtr_val < b.grwng_avg_tmprtr_val) return -1;
            if (a.grwng_avg_tmprtr_val > b.grwng_avg_tmprtr_val) return 1;

            return 0;
        });

      } else {
        this.dataPlantList = [...this.dataPlantListOri];
      }
      
      this.setPlantListMode = true;
      this.setListAll = true;
