<script>
// import BarChart from '../Charts/BarChart'
import storySoilJson from './json/plantKnowledgeSoil.json'
import storyLightJson from './json/plantKnowledgeLight.json'
import storyTempJson from './json/plantKnowledgeTemp.json'

const axios = require("axios");

export default {
  // name: 'App',
  components: { 
          // BarChart, 
          // LineChart 
        },
  data() {
    
    return {
      dataStorySoil: storySoilJson,
      dataStoryLight: storyLightJson,
      dataStoryTemp: storyTempJson,
      
      cropJson: null,
      sidoJson: null,
      sigunguCd: '108',   // default 
      cropChecked:[1], // default

      // chart
      // chartData: {
      //     labels: [ 'January', 'February', 'March' ],
      //     datasets: [ { data: [40, 20, 12] } ]
      // },
      loaded: false,
      chartData: null,
      targetType: 'A',
      storyOn: true,
      storyGbn: 'soil',
    }
  },
  created() {
  },
  beforeMount() {
  },
  async mounted() {
    // this.getSidoList();
    // this.getCropList();
    // this.getWeather();
  },
  methods:{
    testEvent() {
      alert('mouseover!!');
    },
    showExplain(targetType) {
      this.targetType=targetType;
    },  
    async getSidoList(){
      var resultJson = {};
      this.loaded = false;
      const axios = require("axios");
      await axios
        .post("/api/lineChartDb", { contentsId: 19})
        .then((response) => {
          resultJson = response.data;
          this.sidoJson = resultJson[0].get_result_json;
          this.resultKey = Object.keys(this.sidoJson[0]);  // sido, sinungu
        })
        .catch((error) => {
          console.log(error);
        });
    },

    async getCropList(){
      // crop list
      var resultJson = {};
      await axios
          .post("/api/lineChartDb", {contentsId: 17})
          .then((response) => {
            resultJson = response.data;
            this.cropJson = resultJson[0].get_result_json;
          })
          .catch((error) => {
          console.log(error);
          });
    },

    async getWeather(){
      var resultJson = {};
      var crpIdList = '';
      crpIdList = '\'';

      for (var i in this.cropChecked) {
            if (this.cropChecked[i]) {
              console.log(this.cropChecked[i]);
              crpIdList = crpIdList + '' + this.cropChecked[i] + ',';
            }
          }
      crpIdList = crpIdList.slice(0, -1);
      crpIdList = crpIdList + '\'';
      
      this.loaded = false;
      const axios = require("axios");
      await axios
        .post("/api/lineChartDb", { contentsId: 18, sigungu_cd: this.sigunguCd, crp_id_list: crpIdList })
        .then((response) => {
          resultJson = response.data[0].get_result_json;

          this.chartData = { labels: resultJson.lables, datasets: resultJson.datasets };
          this.chartOptions = { responsive: true, maintainAspectRatio: true };

          this.loaded = true;
        })
        .catch((error) => {
          console.log(error);
        });
    }
  }
}
</script>
