          backgroundColor: chartBackgroundColor,
          // borderColor: [
          //   'rgba(255, 99, 132, 1)',
          //   'rgba(54, 162, 235, 1)'
          // ],
          borderWidth: 1,
          // *** datalabel option  
          datalabels: {
            display: true, // default true, false      .
            align: "right",
            anchor: "end",
            color: "#898989",
            borderWidth: 2,
            padding: {
              bottom: 0,
              left: 20,
            },
          },
        }]
      },
